<template>
  <div class="wrap">
    <h1 class="headline">{{ categoryName }}</h1>
    <!--    <div class="tag-lists">-->
    <!--      <div class="tag" v-for="(tag, index) in state.tagList" :key="index">-->
    <!--        <label class="tag-label">-->
    <!--          <input :id="tag" type="checkbox" :value="tag" v-model="state.selectingTags" class="input-tag" />-->
    <!--          {{ tag }}-->
    <!--        </label>-->
    <!--      </div>-->
    <!--    </div>-->
    <h2 class="headline">{{ $t('result.subtitle') }}</h2>
    <CourseItems :courses="courses" class="course-item" v-if="courses?.length" />
    <div v-else>なし</div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue'
import CourseItems from '@/components/CourseItems.vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import resultList from '@/graphql/queries/resultList'
import { ResultListQuery } from '@/graphql/@types/types'
import { useRouter } from 'vue-router'

export default defineComponent({
  components: {
    CourseItems
  },
  setup() {
    const { currentRoute } = useRouter()

    const categoryName = currentRoute.value.query.category
    const { result } = useQuery<ResultListQuery>(resultList, {
      category_name: categoryName
    })

    const courses = useResult(result)

    const state = reactive({
      tagList: ['日本食', '洋食', '中華', 'カフェ', 'スイーツ', 'エスニック'],
      selectingTags: ['日本食', '洋食', '中華', 'カフェ', 'スイーツ', 'エスニック']
    })

    return {
      state,
      categoryName,
      courses
    }
  }
})
</script>

