import { gql } from '@apollo/client/core'

const resultList = gql`
  query ResultList($category_name: String) {
    Courses(where: { Category: { _and: { name: { _eq: $category_name } } } }) {
      id
      image
      title
      Instructor {
        name
      }
      Category {
        name
      }
    }
  }
`

export default resultList
